@import url(https://fonts.googleapis.com/css2?family=Exo+2:wght@500&family=Open+Sans:wght@300;400&display=swap);
body {
  margin: 0;
  color: #DADADA;
  width: 100%;
  min-height: 100%;
  background-color: #191919;
}

.App {
  font-family: 'Open Sans', sans-serif;
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  font-size: 30px;
}

.info-section {
  position: relative;
  width: 100vw;
  margin: 0;
}

.headline {
  font-size: 54px;  
  font-weight: 400;
  margin: 0;
  padding-left: 540px;
  padding-bottom: 70px;
  grid-column: 1 / 3;
  grid-row: 2 / 3;
  display: flex;
  align-items: flex-end;
}

.link:hover {
  text-decoration: none;
  cursor: pointer;
}
.preview {
    position: relative;
    width: 100vw;
    min-height: 100vh;
}

.preview__headline {
    position: absolute;
    top: 446px;
    margin: 0;
    font-family: 'Exo 2', sans-serif;
    font-size: 124px;
    font-weight: 500;
    z-index: 1;
    color: #ffffff;
}

.preview__headline_unloaded {
    left: -1000px;
}

.preview__headline_loaded {
    left: 200px;
    transition: left 2s 2000ms;
}

.preview__photo-container {
    position: absolute;
    right: 0;
    bottom: 0;
    overflow: hidden;
}

.preview__photo-container_unloaded {
    width: 100%;
    height: 100%;
    -webkit-filter: blur(20px);
            filter: blur(20px);
}

.preview__photo-container_loaded {
    width: calc(100% - 540px);
    height: calc(100% - 220px);
    transition: width 2s 2000ms, height 2s 2000ms, -webkit-filter 2s;
    transition: filter 2s, width 2s 2000ms, height 2s 2000ms;
    transition: filter 2s, width 2s 2000ms, height 2s 2000ms, -webkit-filter 2s;
}

.preview__photo {
    position: absolute;
    right: 0;
    bottom: -220px;
}
.header {
    position: fixed;
    top: 0;
    left: 0;
    height: 140px;
    z-index: 2;
    box-sizing: border-box;
    width: 100vw;
    padding-left: 540px;
    padding-right: 500px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgba(25, 25, 25, 0.9);
    
}

.header_unloaded {
    top: -140px;
}

.header_loaded {
    top: 0px;
    transition: top 2s 2000ms;
}

.header__item {
    text-decoration: none;
    font-size: 36px;
    font-weight: 300;
    letter-spacing: -0.02em;
    z-index: 2;
    color: #ffffff;
}

.header__item:hover {
    cursor: pointer;
}

.header__item_active {
    text-decoration: underline;
}
  
.about {
    display: grid;
    grid-template-columns: 770px 1fr;
    grid-template-rows: 0% 300px 1fr;
    grid-column-gap: 40px;
}

.about__photo-container {
    width: 770px;
    height: 780px;
    overflow: hidden;
    grid-column: 1 / 2;
    grid-row: 3 / 4;
}

.about__description {
    grid-column: 2 / 3;
    grid-row: 3 / 4;
    margin: 0;
    font-size: 30px;
    font-weight: 400;
    max-width: 610px;
}

.about__description__item {
    margin: 0 0 35px 0;
    line-height: 111%;
}
.technologies {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 0% 300px 1fr;
    grid-column-gap: 100px;
}

.technologies__list {
    grid-row: 3 / 4;
    grid-column: 1 / 3;
    padding-left: 540px;
    display: flex;
    flex-direction: column;
    max-width: 850px;
    font-size: 30px;
}

.technologies__list__item {
    display: grid;
    grid-template-columns: 25% 75%;
    margin: 0 0 50px 0;
}

.technologies__names__item { 
    font-weight: 600;
    letter-spacing: 0.31em;
    margin-right: 38px;
}

.technologies__values__item {
    font-weight: 400;
    margin-left: 38px;
}

.projects {
    display: grid;
    grid-template-rows: 0% 300px auto;
    grid-template-columns: 1fr;
}

.projects__list {
    display: flex;
    flex-direction: column;
    grid-row: 3 / 4;
    grid-gap: 200px;
    gap: 200px;
}
.project {
    display: grid;
    grid-template-columns: 540px 1fr 1fr 3fr;
    grid-template-rows: 1fr 2fr auto;
}

.project__name {
    grid-column: 2 / 4;
    grid-row: 1 / 2;
    display: flex;
    justify-content: flex-start;
    margin: 0 0 50px 0;
}

.project__number {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    display: flex;
    justify-content: flex-end;
    margin: 0 10px 50px 0;
}

.project__headline {
    font-size: 30px;
    font-weight: 600;
    letter-spacing: 0.31em;
}

.project__links {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    display: flex;
    flex-direction: column;
}

.project__link {
    color: #ffffff;
    font-size: 36px;
    font-weight: 300;
    letter-spacing: -0.02em;
    margin-bottom: 20px;
    width: -webkit-min-content  ;
    width: -moz-min-content  ;
    width: min-content  ;
}

.project__description {
    line-height: 111%;
    max-width: 610px;
    grid-column: 3 / 5;
    grid-row: 2 / 3;
}

.project__photos {
    grid-column: 2 / 5;
    grid-row: 3 / 4;
    display: flex;
    justify-content: start;
    align-items: center;
}
   
.contacts__list {
    grid-row: 2 / 3;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-auto-flow: column;
    margin-left: 540px;
    max-width: 610px;
}

.contacts__list-item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 36px;
    font-weight: 300;
    letter-spacing: -0.02em;
    color: #DADADA;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}
.footer {
    margin: 200px 0 70px 0;
    padding: 0 500px 0 540px;
}

.designer-info {
    display: flex;
    justify-content: space-between;
    margin: 0;
    font-size: 20px;
    line-height: 111%;
    color: #DADADA;
}
